import { Component, OnInit, ViewChild } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { UserSearch } from '../../../../shared-ui/models/global.model';
import { ModalDirective } from 'ngx-bootstrap';

@Component({
  selector: 'app-userlist',
  templateUrl: './userlist.component.html',
  styleUrls: ['./userlist.component.scss']
})
export class UserlistComponent implements OnInit {

  @ViewChild("deleteUserModal", { static: false })
  public deleteUserModal: ModalDirective;
  
  pager: any = {};
  users: any;
  pagination: any;
  multiSelect: any;
  selectedUsers: any = [];
  selectedUser: any;
  order: string = 'name';
  reverse: boolean = false;
  companies: any;
  userSearch: UserSearch = new UserSearch;
  
  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }
  
  constructor(
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
  ) {
    this.globalService.sendActionChildToParent('users'); 
  }

  ngOnInit() {
    this.getCompanies();
    this.getUsers();
  }

  getCompanies(){
    this.spinner.show();
    this.globalService.getCompanies({}).subscribe(data => {
      this.spinner.hide();
      if(Object.keys(data).length){
        this.companies = data.Items;
      }
    }, error => {
      this.toastr.error('There are some server error! Please check connection.', 'Error');
    });
  }

  getGroups(companyId: any){
    this.globalService.getCompanies({}).subscribe(data => {
      this.spinner.hide();
      if(Object.keys(data).length){
        this.companies = data.Items;
      }
    }, error => {
      this.toastr.error('There are some server error! Please check connection.', 'Error');
    });
  }

  getUsers(){
    this.spinner.show();
    this.globalService.getUsers({}).subscribe(data => {
      this.spinner.hide();
      if(Object.keys(data).length){
        this.users = data.items;
        this.pagination = data.meta;
      }
    }, error => {
      this.toastr.error('There are some server error! Please check connection.', 'Error');
    });
  }

  showDeleteModal(user: any){
    this.selectedUser = user;
    this.deleteUserModal.show();
  }

  multiSelectCompany(multiSelect: any){
    this.selectedUsers = [];
    this.users.forEach(element => {
      if(multiSelect){
        element.added = false;
      }else{
        element.added = true;
        this.selectedUsers.push(element)
      }
    });
  }

  selectUser(user: object){
    let found = this.selectedUsers.filter(e => e.id == user['id']);
    if(found.length){
      this.selectedUsers.splice(this.selectedUsers.indexOf(found[0]), 1);
    }else{
      this.selectedUsers.push(user);
    }
  }

  deleteUser(){
    let postData = {
      isActive: 0,
    }
    this.globalService.updateUser(postData, this.selectedUser['id']).subscribe(data => {
      this.deleteUserModal.hide();
    });
  }

  uploadFile($event){
    let file = $event.target.files[0];
    console.log("file ------- ", file);
    this.globalService.uploadFile(file).subscribe(
      (data: any) => {

      }
    );
  }

}
